import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'

Vue.use(Router)

export default new Router({
  linkActiveClass: 'active', //切記加入 才會有 active 的樣式
  routes: [
    {
      path:'*',
      redirect:'/'
    },
    {
      path: '/',
      component: Home,
      children:[
        {   
            name :'Index', //元件名稱
            path :'/',
            component : () => import('./views/Index.vue')
        },
        {   
            name :'AirProducts', //元件名稱
            path :'/air_products',
            component : () => import('./views/AirProducts.vue')
        },
        {   
            name :'CreateOrder', 
            path :'/create_order',
            component : () => import('./views/CreateOrder.vue')
        },
        {   
            name :'CheckOrder', 
            path :'/checkout_order/:orderId',
            component : () => import('./views/CheckOrder.vue')
        },
      ]
    },
    {   
      name :'Login', 
      path :'/login',
      component : () => import('./views/Login.vue')
    },
    {   
      name :'Dashboard', //元件名稱
      path :'/admin',
      component : () => import('./views/Dashboard.vue'),
      children:[
          {   
              name :'Products', //元件名稱
              path :'products',
              component : () => import('./views/Products.vue'),
              meta: {requiresAuth: true},
          },
          {   
              name :'Orders', //元件名稱
              path :'orders',
              component : () => import('./views/Orders.vue'),
              meta: {requiresAuth: true},
          },
          {   
              name :'Coupons', //元件名稱
              path :'coupons',
              component : () => import('./views/Coupons.vue'),
              meta: {requiresAuth: true},
          },
      ],
    }
  ]
})
