import Vue from 'vue'
import axios from 'axios'
import VueAxios from 'vue-axios'
import Loading from 'vue-loading-overlay';
import VeeValidate from 'vee-validate';
import VueI18n from 'vue-i18n';
import zhTW from 'vee-validate/dist/locale/zh_TW';
import 'vue-loading-overlay/dist/vue-loading.css';
import 'bootstrap'
import App from './App.vue'
import router from './router'
import './bus'
import currencyFilter from './components/filters/currency'
import timestampFilter from './components/filters/timestamp'

Vue.config.productionTip = false
Vue.use(VueAxios, axios);
Vue.use(VueI18n); //中文化
const i18n = new VueI18n({
  locale: 'zhTW'
});
Vue.use(VeeValidate,{
  events: 'input|blur', //務必加入
  i18n,
  dictionary: {
    zhTW
  }
});
Vue.component('Loading',Loading);
Vue.filter('currency',currencyFilter);
Vue.filter('timestamp',timestampFilter);


/* eslint-disable no-new */
axios.defaults.withCredentials = true; //admin

new Vue({
  i18n,
  router,
  render: h => h(App)
}).$mount('#app')
